.header {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  &__logo {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 1.37;
    color: inherit;
    padding: 24px 0;
    @media screen and (min-width: $tablet) {
      font-size: 32px;
      line-height: 1.37;
      margin-right: 48px;
      padding: 32px 0 30px 0;
    }
    @media screen and (min-width: $desktop) {
      font-size: 42px;
      line-height: 1.35;
      margin-right: 102px;
      padding: 28px 0;
    }
  }
}

.header--night {
  border-bottom: 1px solid rgba(244, 244, 244, 0.2);
}
.header--menu-open {
  border: none;
}

.header__nav {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.menu-container {
  @media screen and (max-width: $tablet) {
    position: absolute;
    top: calc(100%);
    left: -16px;
    width: $mobile;
    height: calc(100vh - 80px);
    z-index: 999;
    display: none;
    background-color: $light-color;
    // overflow: hidden;
    padding-bottom: 18px;
  }
  @media screen and (min-width: $tablet) {
    position: relative;
    display: flex;
    align-items: center;
  }
  @media screen and (min-width: $desktop) {
  }
}
.menu-container--dark {
  background-color: $bg-dark-color;
}
.menu-container--open {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav-menu {
  padding-top: 68px;
  display: flex;
  flex-direction: column;
  gap: 18px;
  @media screen and (min-width: $tablet) {
    flex-direction: row;
    padding: 0;
    align-items: center;
    margin-right: 32px;
    gap: 44px;
  }
  @media screen and (min-width: $desktop) {
    margin-right: 147px;
    gap: 69px;
  }
}
.nav-menu__link {
  position: relative;
  display: flex;
  align-items: center;
  font-family: 'Manrope';
  font-style: normal;
  color: inherit;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.37;
  @media screen and (max-width: $tablet) {
    padding-top: 6px;
    padding-bottom: 6px;
    padding-left: 16px;
  }
  @media screen and (min-width: $tablet) {
    font-weight: 700;
  }
  @media screen and (min-width: $desktop) {
    font-size: 18px;
    line-height: 1.39;
  }
}
.nav-menu__link:hover,
.nav-menu__link:focus {
  color: $accent-color;
}
.nav-menu__link--active {
  @media screen and (max-width: $tablet) {
    background-color: $accent-color;
    color: $light-color;
  }
}
.nav-menu__link--active::after {
  @media screen and (min-width: $tablet) {
    content: '';
    position: absolute;
    bottom: -5px;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    width: 100%;
    height: 0px;
    border: 1px solid $accent-color;
  }
}
.mobile-menu__icon {
  border: 1px solid white;
  border-radius: 50%;
}
.icon-arrow-right {
  margin-left: 174px;
  @media screen and (min-width: $tablet) {
    display: none;
  }
}

.menu__button {
  height: 24px;
  width: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
  background: transparent;
  fill: inherit;
  @media screen and (min-width: $tablet) {
    display: none;
  }
}
.menu__button.menu__button--open .icon-cross {
  display: block;
}
.menu__button .icon-cross {
  display: none;
}
.menu__button.menu__button--open .icon-menu {
  display: none;
}
.menu__button .icon-menu {
  display: block;
}
.mobile-menu__icon {
  margin-right: 13px;
  @media screen and (min-width: $tablet) {
    display: none;
  }
}
// зашлушка поиска на время
.search {
  @media screen and (max-width: $tablet) {
    display: none;
    position: absolute;
    top: 0px;
    right: 54px;
    width: 20px;
    height: 20px;
  }
  border: 1px solid black;
  border-radius: 35%;
  @media screen and (min-width: $tablet) {
    width: 213px;
    height: 31px;
    flex-direction: row;
    padding: 0;
    align-items: center;
    margin-right: 24px;
  }
  @media screen and (min-width: $desktop) {
    margin-right: 147px;
    width: 288px;
  }
}
